.auth {
    /* background-color: #ff8180; */
    background: #ff6564;
    animation: bgcolor 7s ease infinite;
    height: 100vh;
    width: 100%;
}

@keyframes bgcolor {
    0% {
        background-color: #ff9b9a;
    }
    50% {
        background-color: #ff6564;
    }

    100% {
        background-color: #ff9b9a;
    }
}

.auth_container {
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
    top: 25%;
}

.auth_container > h1 {
    font-size: 7rem;
    color: white;
}

.auth_signUp {
    border-style: none;
    background-color: white;
    color: #ff8180;
    border-radius: 8px;
    font-size: 2rem;
    padding: 10px 40px;
    width: 283px;
    margin-top: 20px;
}

.auth_loginContainer {
    display: flex;
    margin-top: 10px;
    justify-items: space-around;
    align-items: center;
}

.auth_login {
    color: rgba(104, 9, 9, 0.5);
    margin-left: 10px;
    text-decoration: none;
    font-size: 1.3rem;
    font-weight: 400;
}

.auth_loginContainer > p {
    font-size: 1.2rem;
    color: rgba(255, 255, 255, 0.7);
    font-weight: 300;
}
